#root {
  height: 100%; }

.app-wrapper {
  height: 100%; }

.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 2;
  background-color: #1565C0;

  &__table {
    width: 100%; }

  &__button {
    background-color: #1565C0;
    &:hover {
      background-color: #2196F3; }
    &:active {
      background-color: #2196F3; } }

  &__list {
    min-width: 97px; } }

.content-wrapper {
  min-height: 100%;
  box-sizing: border-box;
  margin-bottom: -100px;
  padding-bottom: 100px; }

.alert {
  padding: .75rem 1.25rem;
  border-radius: .25rem;
  border: 1px solid transparent;
  position: absolute;
  left: 0;
  bottom: 100px;
  transform: translate(-100%);

  &_success {
    color: #155724;
    background-color: #d4edda;
    border-color: #c3e6cb;
    animation: showHideAlert 3.6s ease; }

  &_failure {
    color: #721c24;
    background-color: #f8d7da;
    border-color: #f5c6cb;
    animation: showHideAlert 3.6s ease; }

  @keyframes showHideAlert {
    0%, 100% {
      transform: translate(-100%); }
    10%, 90% {
      transform: translate(20px); } } }

.footer {
  box-sizing: border-box;
  height: 100px;
  background-color: #eee;
  border-top: 1px solid #e0e0e0;
  padding-top: 35px; }
